<template>
  <div id="db" class="grid">
    <div class="grid__col--3">
      <h1>{{ currentMonthName }}</h1>

      <ExpenseForm
        :submitHandler="recordExpense"
        :editingExpense="editingExpense"
      />

      <fieldset>
        <legend>Income settings</legend>

        <label>
          Income each payday
          <money v-model="amountPaid" v-bind="money"></money>
        </label>

        <label>
          First pay day
          <input
            type="date"
            v-model="formattedPayDay1"
          />
        </label>

        <label>
          Second pay day
          <input
            type="date"
            v-model="formattedPayDay2"
          />
        </label>
      </fieldset>
    </div>

    <div class="grid__col--9">
      <div class="table" ref="expenseTable">
        <div class="table__head">
          <div class="table__row">
            <div class="
                table__cell
                table__cell--head
                table__cell--numeric
              "
            >
              Amount
            </div>

            <div class="table__cell table__cell--head">
              Category
            </div>

            <div class="table__cell table__cell--head">
              &nbsp;
            </div>
          </div>
        </div>

        <div class="table__body">
          <ExpenseRow
            v-for="(expense, i) in expenses"
            :editClickHandler="editExpense"
            :deleteClickHandler="deleteExpense"
            :key="i"
            :expense="expense"
            :i="i" />
        </div>

        <div class="table__foot">
          <div class="table__row">
            <div class="table__cell">
              <strong>Total</strong>
              <Currency :value="amountSpent" />
            </div>

            <div class="table__cell">
              &nbsp;
            </div>

            <div class="table__cell">
              <strong>Remaining:</strong>
              <Currency :value="amountRemaining" />
            </div>
          </div>
        </div>
      </div>

      <p>
        <strong>Next pay day:</strong>
        {{ daysUntilNextPayDay }}
      </p>

      <p>
        <strong>You can spend:</strong>
        <Currency :value="amountPerDay" />
        per day
      </p>
    </div>
  </div>
</template>

<script>
  import moment from 'moment';

  import Storage from './Storage';

  import { Money } from 'v-money';
  import Currency from './Currency';

  import ExpenseForm from './ExpenseForm';
  import ExpenseRow from './ExpenseRow';

  export default {
    data () {
      return {
        currentMonthName: moment().format("MMMM"),
        editingExpense: null,
        editingRow: null,
        expenses: Storage.get("expenses", {
          parser: "JSON",
          default: [],
        }),
        payDay1: moment("2018-01-12"),
        payDay2: moment("2018-01-31"),
        amountPaid: Storage.get("amountPaid", {
          parser: "FLOAT",
          default: 0,
        }),
        money: {
          decimal: '.',
          thousands: ',',
          prefix: '$',
          suffix: '',
          precision: 2,
          masked: false,
        },
      };
    },

    components: {
      Money,
      Currency,
      ExpenseForm,
      ExpenseRow,
    },

    watch: {
      amountPaid: function(value) {
        Storage.set("amountPaid", value);
      },

      expenses: function(currentExps, previousExps) {
        Storage.set(
          "expenses",
          this.expenses,
          { formatter: "JSON" }
        );

        if (currentExps.length > previousExps.length) {
          var row = this.$refs
            .expenseTable
            .querySelector(".table__body .table__row:first-child");

          row.classList.add(
            "table__row--inserted",
            "table__row--highlighted"
          );

          setTimeout(function() {
            row.classList.remove("table__row--highlighted");

            setTimeout(function() {
              row.classList.remove("table__row--inserted");
            }, 1510);
          });
        }
      },
    },

    computed: {
      amountSpent: function() {
        return this.expenses.reduce(function(acc, exp) {
          return acc + parseFloat(exp.amount);
        }, 0).toFixed(2);
      },

      formattedPayDay1: {
        get: function() {
          return this.payDay1.format("YYYY-MM-DD");
        },

        set: function(value) {
          this.payDay1 = moment(value);
        },
      },

      formattedPayDay2: {
        get: function() {
          return this.payDay2.format("YYYY-MM-DD");
        },

        set: function(value) {
          this.payDay2 = moment(value);
        },
      },

      amountRemaining: function() {
        return parseFloat(
          this.amountPaid - this.amountSpent
        ).toFixed(2);
      },

      amountPerDay: function() {
        return (
          this.amountRemaining / this.getDaysRemaining()
        ).toFixed(2);
      },

      daysUntilNextPayDay: function() {
        if (moment().startOf("day") < this.payDay1.startOf("day")) {
          return this.payDay1.startOf("day")
            .from(moment().startOf("day"));
        } else {
          return this.payDay2.startOf("day")
            .from(moment().startOf("day"));
        }
      },
    },

    methods: {
      recordExpense: function(expense) {
        var expenses = this.expenses.slice(0);

        if (this.editingRow != null) {
          expenses[this.editingRow] = expense;
        } else {
          expenses.unshift(expense);
        }

        this.editingRow = null;
        this.expenses = expenses;
      },

      deleteExpense: function(i) {
        this.expenses.splice(i, 1);
      },

      editExpense: function(i) {
        this.editingRow = i;
        this.editingExpense = this.expenses[this.editingRow];
      },

      getDaysRemaining: function() {
        if (moment() < this.payDay1) {
          return this.payDay1.startOf("day")
            .diff(moment().startOf("day"), "days");
        } else {
          return this.payDay2.startOf("day")
            .diff(moment().startOf("day"), "days");
        }
      },
    },
  }
</script>

<style lang="scss">
  #db {
    font-family: Helvetica Neue, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }

  h1, h2 {
    font-weight: normal;
  }

  ul {
    list-style-type: none;
    padding: 0;
  }

  li {
    display: inline-block;
    margin: 0 10px;
  }

  a {
    color: #42b983;
  }

  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    margin: 0 0 1rem;
    padding: 0;
  }

  fieldset {
    margin: 0 0 1rem;
    padding: 1rem;
  }

  label,
  input {
    display: block;
  }

  input {
    font-size: 1.25rem;
    padding: 0.5rem 1rem;
  }

  label {
    margin: 0 0 1rem;
  }

  .field {
    transition: background-color 1.5s;
  }

  .field--error {
    background-color: Pink;
    transition: none;
  }

  .table {
    border: 1px solid #999;
  }

  .table__row {
    display: flex;
    flex-direction: row;
    border-bottom: 1px solid #999;
    transition: background-color 0.2s;
  }

  .table__row--inserted {
    transition: background-color 1.5s;
  }

  .table__row--highlighted {
    background-color: LightSteelBlue;
    transition: none;
  }

  .table__body .table__row:nth-child(even) {
    background: rgba(0, 0, 0, 0.025);
  }

  .table__body .table__row:hover {
    background: rgba(0, 0, 0, 0.09);
  }

  .table__row:hover {
    .action {
      opacity: 1;
    }
  }

  .table__head {
    background: rgba(0, 0, 0, 0.1);
    box-shadow: 0 0.2rem 0.5rem rgba(0, 0, 0, 0.2);
  }

  .table__body {
    height: 250px;
    overflow-y: auto;
    overflow-x: hidden;
  }

  .table__foot {
    background: rgba(0, 0, 0, 0.1);
    box-shadow: 0 -0.2rem 0.5rem rgba(0, 0, 0, 0.2);
  }

  .table__cell--head {
    font-weight: bold;
  }

  .table__cell {
    width: 50%;
    padding: 0.5rem 1rem;
  }

  .table__cell--numeric {
    text-align: right;
  }

  .table__cell--colspan-2 {
    width: 100%;
  }

  .table__foot {
    text-align: right;
  }

  .action {
    opacity: 0;
    display: inline-block;
    padding: 0 0.5rem;
    margin: 0;
    cursor: pointer;
    transition: color 0.2s, opacity 0.2s;
  }

  .action::selection {
    background: none;
  }

  .action--edit {
    font-size: 0.9rem;
    font-weight: normal;
    color: CadetBlue;
  }

  .action--remove {
    margin-top: 3px;
    color: red;
  }

  .action--remove:hover {
    color: darkred;
  }

  .action--edit:hover {
    color: Teal;
  }

  [class*="grid__col--"] {
    margin-right: 1rem;
  }

  @media (min-width: 625px) {
    .grid {
      display: flex;
      width: 100%;
    }

    .grid__col--3 {
      width: 25%;
    }

    .grid__col--6 {
      width: 50%;
    }

    .grid__col--9 {
      width: 75%;
    }

    [class*="grid__col--"]:last-child,
    [class*="grid__col--"]:only-child {
      margin-right: 0;
    }
  }
</style>



