<template>
  <div class="table__row">
    <div class="table__cell table__cell--numeric">
      <Currency :value="expense.amount" />
    </div>

    <div class="table__cell">
      {{ expense.category }}
    </div>

    <div class="table__cell">
      <icon
        name="edit"
        class="action action--edit"
        @click.native="editClickHandler(i)"
      ></icon>

      <icon
        name="times"
        class="action action--remove"
        @click.native="deleteClickHandler(i)"
      ></icon>
    </div>
  </div>
</template>

<script>
  import 'vue-awesome/icons';
  import Icon from 'vue-awesome/components/Icon';
  import Currency from './Currency.vue';

  export default {
    name: "ExpenseRow",
    props: {
      editClickHandler: {
        required: false,
        type: Function,
        default: () => { return false; },
      },

      deleteClickHandler: {
        required: false,
        type: Function,
        default: () => { return false; },
      },

      expense: {
        required: true,
        type: Object,
      },

      i: {
        required: true,
        type: Number,
      },
    },

    components: {
      Icon,
      Currency,
    },
  };
</script>



